import { Inject, Injectable } from '@angular/core';
import { Router, Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { BriefUser } from '../../../../view-model/brief-user';
import { User } from '../user.model';
import { IResponseResult } from '../../../../interfaces/iresponse-result';
import { APP_CONFIG, IAppConfig } from '../../../../configs/app.config';

@Injectable()
export class UserService implements Resolve<any> {
    private url = 'user/';

    constructor(@Inject(APP_CONFIG) appConfig: IAppConfig,
                private http: HttpClient) {
        this.url = `${appConfig.HR_API_URL}${this.url}`;
    }

    resolve(route: ActivatedRouteSnapshot, state: Object) {
        const queryParams = route.queryParams;
        const keyword = queryParams.keyword;
        const status = queryParams.status;
        const isActive = queryParams.isActive;
        const isAdmin = queryParams.isAdmin;
        const officeIdPath = queryParams.officeIdPath;
        const page = queryParams.page;
        const pageSize = queryParams.pageSize;
        return this.search(keyword, status, isActive, isAdmin, officeIdPath, page, pageSize);
    }

    search(keyword: string, status?: string, isActive?: boolean, isAdmin?: boolean, officeIdPath?: string,
           page: number = 1, pageSize: number = 20): Observable<any> {
        const params = new HttpParams()
            .set('keyword', keyword ? keyword : '')
            .set('status', status ? status : '')
            .set('isActive', isActive != null ? isActive.toString() : '')
            .set('isAdmin', isAdmin != null ? isAdmin.toString() : '')
            .set('officeIdPath', officeIdPath ? officeIdPath : '')
            .set('page', page ? page.toString() : '1')
            .set('pageSize', pageSize ? pageSize.toString() : '20');

        return this.http.get(`${this.url}search`, {
            params: params
        });
    }

    searchForSelect(keyword: string, page: number = 1, pageSize: number = 20): Observable<any> {
        const params = new HttpParams()
            .set('keyword', keyword)
            .set('page', page.toString())
            .set('pageSize', pageSize.toString());

        return this.http.get(`${this.url}search-for-select`, {
            params: params
        });
    }

    getDetail(id: string): Observable<User> {
        return this.http.get(`${this.url}detail`, {
            params: new HttpParams().set('id', id)
        }) as Observable<User>;
    }

    insert(user: User): Observable<IResponseResult> {
        return this.http.post(`${this.url}insert`, user) as Observable<IResponseResult>;
    }

    quickInsert(fullName: string, userName: string, password: string, cardNumber: string): Observable<number> {
        return this.http.post(`${this.url}quick-insert`, '', {
            params: new HttpParams()
                .set('fullName', fullName)
                .set('userName', userName)
                .set('password', password)
                .set('cardNumber', cardNumber)
        }) as Observable<number>;
    }

    update(user: User): Observable<IResponseResult> {
        return this.http.post(`${this.url}update`, user) as Observable<IResponseResult>;
    }

    updateAvatar(userId: string, avatar: string): Observable<IResponseResult> {
        return this.http.post(`${this.url}update-avatar`, '', {
            params: new HttpParams()
                .set('userId', userId)
                .set('avatar', avatar)
        }) as Observable<IResponseResult>;
    }

    delete(id: string): Observable<IResponseResult> {
        return this.http.post(`${this.url}delete`, id) as Observable<IResponseResult>;
    }

    export(officeIdPath?: number) {
        const url = `${this.url}export-user?officeIdPath=${officeIdPath == null ? '' : officeIdPath}`;
        return this.http.get(url, {responseType: 'blob'})
            .pipe(map(response => {
                return new Blob([response,
                    {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel'}]);
            }));
    }

    exportLabor(officeIdPath?: number, fileName?: string) {
        const url = `${this.url}export-labor-contract?officeIdPath=${officeIdPath == null ? '' : officeIdPath}`;
        return this.http.get(url, {responseType: 'blob'})
            .pipe(map(response => {
                return new Blob([response,
                    {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel'}]);
            }));
    }

    exportRecord(officeIdPath?: number, fileName?: string) {
        const url = `${this.url}export-user-record?officeIdPath=${officeIdPath == null ? '' : officeIdPath}`;
        return this.http.get(url, {responseType: 'blob'})
            .pipe(map(response => {
                return new Blob([response,
                    {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel'}]);
            }));
    }

    changePassword(oldPassword: string, newPassword: string) {
        return this.http.post(`${this.url}change-password`, '', {
            params: new HttpParams()
                .set('oldPassword', oldPassword)
                .set('newPassword', newPassword)
        });
    }

    getUserId(): Observable<{ id: string, enrollNumber: number }> {
        return this.http.get(`${this.url}generate-user-id`) as Observable<{ id: string, enrollNumber: number }>;
    }

    searchEthnic() {
        return this.http.get(`${this.url}search-ethnic`);
    }

    getUserByOfficeIdAndChild(officeId: number, isChild: boolean): Observable<any> {
        return this.http.get(`${this.url}search-user-by-officeId-and-isChild`, {
            params: new HttpParams()
                .set('officeId', officeId.toString())
                .set('isChild', isChild.toString())
        }) as Observable<any>;
    }

    getUserByUserId(userId: string): Observable<any> {
        return this.http.get(`${this.url}search-user-by-userId`, {
            params: new HttpParams().set('userId', userId)
        }) as Observable<any>;
    }

    searchForSuggestion(keyword: string, officeIdPath?: string, page: number = 1, pageSize: number = 20): any {
        return this.http.get(`${this.url}search-for-suggestion`, {
            params: new HttpParams()
                .set('keyword', keyword ? keyword : '')
                .set('officeIdPath', officeIdPath ? officeIdPath : '')
                .set('page', page ? page.toString() : '1')
                .set('pageSize', pageSize ? pageSize.toString() : '20')
        });
    }
}
