import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    ElementRef,
    Renderer,
    ViewChild,
    forwardRef, HostListener
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';

import { NhSuggestionService } from './nh-suggestion.service';

@Component({
    selector: 'nh-suggestion',
    templateUrl: './nh-suggestion.component.html',
    providers: [
        { provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => NhSuggestionComponent), multi: true }
    ]
})
export class NhSuggestionComponent implements OnInit, ControlValueAccessor {
    @ViewChild('suggestionInput') suggestionInput: ElementRef;
    @Input() material = false;
    @Input() required = false;
    @Input() url: string;
    @Input() placeholder = 'Nhp t kha tm kim';
    @Input() value: string;
    @Output() onSelectItem = new EventEmitter();
    @Output() onTyping = new EventEmitter();
    @Input() clearAfterSelect = false;

    isShowMenu = false;
    isSearching = false;
    listItems = [];
    searchTerm = new Subject<string>();
    private _text: string;
    propagateChange: any = () => {
    }

    get text() {
        return this._text;
    }

    set text(value) {
        this._text = value;
        this.value = value;
    }

    constructor(private el: ElementRef,
        private renderer: Renderer,
        private suggestionService: NhSuggestionService) {

        this.searchTerm
            .pipe(debounceTime(300))
            .subscribe(term => {
                this.isShowMenu = true;
                this.isSearching = true;
                this.suggestionService.search(this.url, term)
                    .subscribe((result) => {
                        setTimeout(() => {
                            this.renderer.invokeElementMethod(this.suggestionInput.nativeElement, 'focus');
                        }, 100);
                        this.isSearching = false;
                        this.isShowMenu = true;
                        this.listItems = result.map((item, index) => {
                            const obj = item;
                            obj.index = index;
                            obj.selected = false;
                            return obj;
                        });
                    });

            });
    }

    ngOnInit() {

    }

    registerOnTouched(value) {

    }

    registerOnChange(fn) {
        this.propagateChange = fn;
    }

    writeValue(value) {
        if (!value) {
            this.text = '';
        } else {
            this.text = value;
        }
    }

    onSearchInputFocus(e) {
        const value = e.target.value;
        if (!value && this.listItems.length === 0) {
            this.searchTerm.next('');
        }
    }

    onKeyUp(e: any) {
        const keyCode = e.keyCode;
        // Navigate
        if (keyCode === 27) {
            this.isShowMenu = false;
        }

        if (keyCode === 27 || keyCode === 17 || e.ctrlKey) {
            return;
        }

        if (keyCode === 37 || keyCode === 38 || keyCode === 39 || keyCode === 40 || keyCode === 13) {
            this.navigate(keyCode);
            e.preventDefault();
            return false;
        } else {
            const value = e.target.value;
            this.onTyping.emit({ id: -1, name: value });
            if (value !== '') {
                this.searchTerm.next(value);
            } else {
                this.text = '';
                this.listItems = [];
                this.isShowMenu = false;
            }
        }
    }

    selectItem(item: { id: number, name: string }) {
        if (!this.clearAfterSelect) {
            this.value = item.name;
            this.text = item.name;
        } else {
            this.value = '';
            this.text = '';
        }
        this.onSelectItem.emit(item);
        this.propagateChange(item.id);
        this.isShowMenu = false;
    }

    @HostListener('document:click', ['$event'])
    onDocumentClick(el) {
        if (!this.el.nativeElement.contains(el.target)) {
            this.isShowMenu = false;
        }
    }

    private navigate(key: number) {
        const selectedItem = this.listItems.find((item) => {
            return item.selected;
        });

        switch (key) {
            case 37:
                this.back(selectedItem);
                break;
            case 38:
                this.back(selectedItem);
                break;
            case 39:
                this.next(selectedItem);
                break;
            case 40:
                this.next(selectedItem);
                break;
            case 13:
                if (selectedItem) {
                    this.selectItem(selectedItem);
                    this.propagateChange(selectedItem.id);
                    this.onSelectItem.emit(selectedItem);
                }
                break;
        }
    }

    private next(selectedItem: any) {
        if (!selectedItem) {
            const firstItem = this.listItems[0];
            if (firstItem) {
                firstItem.selected = true;
            }
        } else {
            let index = selectedItem.index + 1;
            if (index > this.listItems.length - 1) {
                index = 0;
            }

            const currentItem = this.listItems[index];
            this.resetSelectedStatus();
            currentItem.selected = true;
        }
    }

    private back(selectedItem: any) {
        if (!selectedItem) {
            const lastItem = this.listItems[this.listItems.length - 1];
            if (lastItem) {
                lastItem.selected = true;
            }
        } else {
            let index = selectedItem.index - 1;
            if (index < 0) {
                index = this.listItems.length - 1;
            }

            const currentItem = this.listItems[index];
            this.resetSelectedStatus();
            currentItem.selected = true;
        }
    }

    private resetSelectedStatus() {
        this.listItems.forEach((item) => item.selected = false);
    }
}
